import React from "react";
import "./Portfolio.css";
import Reference from "../primitives/Reference";

export interface PortfolioItem {
  link: string;
  title: string;
  time: string;
  description: string;
}

export default function Portfolio({ data }: { data: PortfolioItem[] }) {
  return (
    <section className="mh-blog image-bg featured-img-two" id="mh-blog">
      <div className="img-color-overlay">
        <div className="container">
          <div className="row section-separator">
            <div className="col-sm-12 section-title">
              <h3>Side projects</h3>
            </div>
            {data.map((x) => {
              return (
                <div className="col-sm-12 col-md-4">
                  <div className="mh-blog-item dark-bg">
                    <img
                      src="assets/images/b-3.png"
                      alt=""
                      className="img-fluid"
                    />
                    <div className="blog-inner">
                      <h2>
                        <Reference to={x.link} text={x.title} />
                      </h2>
                      <div className="mh-blog-post-info">
                        <ul>
                          <li>
                            <Reference to={x.link} text={x.time} />
                          </li>
                        </ul>
                      </div>
                      <p>{x.description}</p>

                      <Reference to={x.link} text={"Read more"} />
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}
