import React, { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import { useParams } from "react-router-dom";

export default function Page({ getData }) {
  const { id } = useParams();
  const data = getData(id);
  const [content, setContent] = useState(null);
  const [isLoading, setLoader] = useState(true);

  useEffect(() => {
    if (data.content) {
      data.content().then((x) => {
        setContent(x);
        setLoader(false);
      });
    } else {
      setLoader(false);
    }
  }, [id, data]);

  if (isLoading) {
    return <p>Loading...</p>;
  }

  return data.contentFn ? (
    <data.contentFn />
  ) : (
    <ReactMarkdown
      source={content}
      transformImageUri={(uri) =>
        uri.startsWith("http")
          ? uri
          : `${
              process.env.REACT_IMAGE_BASE_URL || "http://localhost:3000/images/"
            }${uri}`
      }
    />
  );
}
